<template>
	<view class="video-component">
		<view class="video-list">
			<scroll-view scroll-y="true">
				<list-video-item v-for='item in list' :key='item.id' :item='item'></list-video-item>
			</scroll-view>
		</view>
	</view>
</template>

<script>
	import {
		getRecommendMv
	} from '@/apis/videopage/index.js'
	import ListVideoItem from './components/ListVideoItem.vue'
	export default {
		components: {
			ListVideoItem
		},
		data() {
			return {
				list: [],
				pagination: {
					page: 1,
					limit: 20
				}
			}
		},
		mounted() {
			this.loadList()
		},
		methods: {
			loadList() {
				return getRecommendMv().then(res => {
					this.list = res.result
				})
			}
		}
	}
</script>

<style scoped>
	scroll-view {
		height: calc(100vh - 90rpx);

		.uni-scroll-view-content {
			display: block;
		}
	}
</style>
